import sassMap from './index.module.scss'
import React, { useEffect, useState } from 'react';
import {useNavigate} from 'react-router-dom'
import {LeftOutlined} from '@ant-design/icons'
import {useSelector} from "react-redux";
import {getMapHomes,getMapHomeItem} from '../../api/mapHomes'
import { Drawer } from 'antd';

export default function MapTp(){
    let navigate = useNavigate()
    const {label,pinyin,short,value} = useSelector((state)=>state.commodity)
    let clicNum = 0
    const [open, setOpen] = useState(false);
    let [homeItem,setHomeItem] = useState([])

    async function setMap(city){
        clicNum += 1
        let cityInfo = city.body
        let map = new window.BMapGL.Map("baiduMap");
        let point = new window.BMapGL.Point(cityInfo[0].coord.longitude,cityInfo[0].coord.latitude);
        map.centerAndZoom(point, 11+(clicNum-1)*2);
        var opts = {
            offset: new window.BMapGL.Size(60, 30),
            anchor:window.BMAP_ANCHOR_BOTTOM_LEFT
        }
        map.addControl(new window.BMapGL.ScaleControl(opts));
        var opts = {
            offset: new window.BMapGL.Size(20, 30),
            anchor:window.BMAP_ANCHOR_BOTTOM_RIGHT
        }
        map.addControl(new window.BMapGL.ZoomControl(opts));
        await cityInfo.forEach(item=>{
            let point = new window.BMapGL.Point(item.coord.longitude,item.coord.latitude)
            let content = `<div>${item.label}<span>${item.count}套</span></div>`;
            let label = new window.BMapGL.Label(content, {       // 创建文本标注
                position: point,
                offset: new window.BMapGL.Size(10, 20)
            })  
            map.addOverlay(label);                        // 将标注添加到地图中
            let labeType = {
                display:'block',
                color: 'white',
                fontSize: '14px',
                width:'80px',
                height:'80px',
                border: '2px solid white',
                backgroundColor:'#1db978',
            }
            if(clicNum == 3){
                document.querySelector('#baiduMap').className = sassMap.unBordeBox
                labeType = Object.assign({},labeType,{height:'30px',width:'100px',border:'0px',lineHeight:'30px'})
                label.addEventListener("click", function(){ 
                    setOpen(true);
                    getMapHomeItem(item.value).then(res=>{
                        setHomeItem(res.body.list)
                    })
                })
            }else{
                document.querySelector('#baiduMap').className = sassMap.bordeBox
                label.addEventListener("click", function(){  
                    getMapHomes(item.value).then(res=>{
                        setMap(res)
                    })
                })
            }
            label.setStyle(labeType)
            
        })
    }
    const onClose = () => {
        setOpen(false);
      };

    useEffect(()=>{
        getMapHomes(value).then(res=>{
            setMap(res)
        })
    },[])
    
    return <div className={sassMap.MapBox}>
        <div>
            <LeftOutlined onClick={()=>{
                navigate(-1)
            }}/>
            <p>地图</p>
            <div></div>
        </div>
        <Drawer
            title={<div className={sassMap.itemInfo}><p>房屋列表</p><p>更多房源</p></div>}
            placement='bottom'
            closable={false}
            onClose={onClose}
            open={open}
        >
            {homeItem.map((item,index)=>{
                return <div key={index} className={sassMap.homesItem}>
                    <div><img src={'http://8.129.190.90:8080'+item.houseImg}/></div>
                    <div>
                        <p>{item.title}</p>
                        <span>{item.desc}</span>
                        <div>
                            {item.tags.map((ite,inde)=>{
                                return <div key={inde}>{ite}</div>
                            })}
                        </div>
                        <p>{item.price}<span>元/月</span></p>
                    </div>
                </div>
            })}
        </Drawer>
        <div id='baiduMap'></div>
    </div>
}